.App {
  text-align: center;
}

.calculator {
  position: relative;
  z-index: 2;
}

h1 {
  color: #f1faee;
  font-size: 2.5rem;
  margin-top: 5px;
  margin-bottom: 5px;
}

button {
  background: #b7943c;
  border-radius: 30px;
  border: none;
  color: #f1faee;
  font-size: 15px;
  font-weight: bold;
  padding: 8px 20px 9px 20px;
  text-decoration: none;
  -webkit-transition: background 0.3s, -webkit-transform 0.2s;
  transition: background 0.3s, -webkit-transform 0.2s;
  -o-transition: background 0.3s, transform 0.2s;
  transition: background 0.3s, transform 0.2s;
  transition: background 0.3s, transform 0.2s, -webkit-transform 0.2s;
}

button:hover {
  text-decoration: none;
  cursor: pointer;
  background: #927630;
}

button:active {
  -webkit-transform: scale(0.9, 0.9);
      -ms-transform: scale(0.9, 0.9);
          transform: scale(0.9, 0.9);
}

@media only screen and (max-width: 600px) {
  button {
    padding: 6px 15px 7px 15px;
  }
}

.react-toggle {
  -ms-touch-action: pan-x;
      touch-action: pan-x;

  display: inline-block;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

.react-toggle-screenreader-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.react-toggle--disabled {
  cursor: not-allowed;
  opacity: 0.5;
  -webkit-transition: opacity 0.25s;
  -o-transition: opacity 0.25s;
  transition: opacity 0.25s;
}

.react-toggle-track {
  width: 50px;
  height: 24px;
  padding: 0;
  border-radius: 30px;
  background-color: #b7943c;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.react-toggle:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #b7943c;
}

.react-toggle--checked .react-toggle-track {
  background-color: #b7943c;
}

.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #b7943c;
}

.react-toggle-track-check {
  position: absolute;
  width: 14px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  left: 8px;
  opacity: 0;
  -webkit-transition: opacity 0.25s ease;
  -o-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-check {
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  -o-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle-track-x {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 0px;
  bottom: 0px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 0;
  right: 10px;
  opacity: 1;
  -webkit-transition: opacity 0.25s ease;
  -o-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.react-toggle--checked .react-toggle-track-x {
  opacity: 0;
}

.react-toggle-thumb {
  -webkit-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -o-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  position: absolute;
  top: 1px;
  left: 1px;
  width: 22px;
  height: 22px;
  border: 1px solid #4d4d4d;
  border-radius: 50%;
  background-color: #fafafa;

  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}

.react-toggle--checked .react-toggle-thumb {
  left: 27px;
  border-color: #b7943c;
}

.react-toggle--focus .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 3px 2px #0099e0;
  box-shadow: 0px 0px 2px 3px #0099e0;
}

.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 5px 5px #0099e0;
  box-shadow: 0px 0px 5px 5px #0099e0;
}

@media screen and (max-width: 480px) {
  body {
    font-size: 0.7em;
  }
  h1 {
    font-size: 1.5em;
  }
  button {
    font-size: 0.7em;
  }
  h3 {
    font-size: 1em;
  }
  .react-toggle {
    -webkit-transform: scale(0.6);
        -ms-transform: scale(0.6);
            transform: scale(0.6);
    position: relative;
    margin: -5px;
  }
  input {
    position: relative;
    top: 3px;
  }
}

#verText {
  padding: 0px;
  margin-bottom: 5px;
}

#gameVersion {
  padding-bottom: 10px;
  padding-top: 10px;
}

.titles {
  font-weight: 900;
  color: #f1faee;
}

.modalButton {
  position: fixed;
  bottom: 20px;
  right: 20px;
  -webkit-clip-path: circle();
          clip-path: circle();
}

.required {
  font-style: italic;
  font-size: 0.75rem;
  color: #f1faee;
}



